.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.movies {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.movie {
  width: 23%;
  margin: 5px;
  padding: 5px;
}

.hidden {
  display: none;
}

.movie:hover .hidden {
  display: block;
}
.input {
  width: 100%;
}

.card-para {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.products {
  justify-content: center;
}

.product {
  margin: 20px;
}
a {
  color: initial;
  text-decoration: none;
}
